<template>
  <div>
    <b-container fluid>
      <b-row>
        <b-col lg="8">
          <b-container fluid="xs" id="basicInfo-container">
            <b-row align-v="center">
              <b-col md="2"><span style="color:red;">*</span></b-col>
              <b-col md="10">
                <b-form-input type="text" v-model="recipe.title" required></b-form-input>
              </b-col>
            </b-row>
            <b-row>
              <b-col md="2"></b-col>
              <b-col md="10">
                <b-form-textarea type="textarea" v-model="recipe.description" required></b-form-textarea>
              </b-col>
            </b-row>
            <b-row align-v="center">
              <b-col md="2"><span style="color:red;">*</span></b-col>
              <b-col md="3">
                <b-form-select v-model="recipe.category_id" :options="categoryOpt" required></b-form-select>
              </b-col>
            </b-row>
            <b-row align-v="center">
              <b-col md="2"></b-col>
              <b-col md="3">
                <b-form-select v-model="recipe.servings" :options="servingOpt" required></b-form-select>
              </b-col>
              <b-col md="3">
                <b-form-select v-model="recipe.cooking_time" :options="timeOpt" required></b-form-select>
              </b-col>
              <b-col md="3">
                <b-form-select v-model="recipe.level" :options="levelOpt" required></b-form-select>
              </b-col>
              <b-col md="1"></b-col>
            </b-row>
						<b-row align-v="center">
							<b-col md="2"></b-col>
							<b-col>
								<b-form-tags 
								input-id="tags-remove-on-delete"
								:input-attrs="{ 'aria-describedby': 'tags-remove-on-delete-help' }"
								v-model="recipe.tag"
								separator=" "
								tag-variant="warning"
								remove-on-delete no-add-on-enter tag-pills
								class="mb-2"></b-form-tags>
								<b-form-text id="tags-remove-on-delete-help">
									<b-icon class="mr-1" icon="exclamation-circle-fill" variant="secondary"></b-icon>
									<kbd>Space</kbd>   <kbd>Backspace</kbd>   .
								</b-form-text>
							</b-col>
						</b-row>
          </b-container>
        </b-col>
        <b-col lg="4">
          <b-container fluid="lg">
            <b-row>
              <b-col>
                <b-form-file enctype="multipart/form-data" v-model="recipe.main_image_file" accept="image/*" placeholder=" "
                  @change="setThumbnail"></b-form-file>
              </b-col>
            </b-row>
            <b-row>
              <b-col>
                <b-img v-if="recipe.main_image_file!=null" :src="imageUrl" height="200px" />
                <span v-else></span>
              </b-col>
            </b-row>
          </b-container>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
		name: 'RecipeInfo',
		computed: {
            ...mapState('editor', ['recipe']),
            ...mapState('recipes', ['selectedRecipe'])
		},
    data() {
      return {
        imageUrl: null,
        categoryOpt: [
					{ value: 0, text: '--  --', disabled: true },
					{ value: 1, text: '' },
					{ value: 2, text: '' },
					{ value: 3, text: '/' },
					{ value: 4, text: '' },
					{ value: 5, text: '' },
					{ value: 6, text: '' },
					{ value: 7, text: '' },
					{ value: 8, text: '' }
				],
				servingOpt: [
					{ value: 0, text: '--  --', disabled: true },
					{ value: 1, text: '' },
					{ value: 2, text: '' },
					{ value: 3, text: '3~4' },
					{ value: 4, text: '5 ' }
				],
        timeOpt: [
					{ value: 0, text: '--  --', disabled: true },
					{ value: 1, text: '15 ' },
					{ value: 2, text: '15~30' }, 
					{ value: 3, text: '30~1' },
					{ value: 4, text: '1~2' },
					{ value: 5, text: '2 ' },
				],
        levelOpt: [
					{ value: 0, text: '--  --', disabled: true },
					{ value: 1, text: '' },
					{ value: 2, text: '' }, 
					{ value: 3, text: '' },
					{ value: 4, text: '' },
					{ value: 5, text: '' },
				],
      }
    },
    methods: {
        setThumbnail(e) {
					const file = e.target.files;
					if (file.length == 0) {
            this.imageUrl = null
            return;
          }
          this.imageUrl = URL.createObjectURL(file[0])
        },
        inputInitialValue() {
            this.recipe.title = this.selectedRecipe.title
            this.recipe.description = this.selectedRecipe.description
            this.recipe.category_id = this.selectedRecipe.category_id
            this.recipe.servings = this.selectedRecipe.servings
            this.recipe.cooking_time = this.selectedRecipe.cooking_time
            this.recipe.level = this.selectedRecipe.level
            this.recipe.tag = this.selectedRecipe.tag
            this.imageUrl = this.selectedRecipe.main_image
        }
    },
    created() {
        this.inputInitialValue()
    }
}
</script>

<style>
img { display: block; margin: 0px auto; }
</style>